const Portfolio = () => {

    
  
    const [windowSize, setWindowSize] = useState(getWindowSize());
  
    useEffect(() => {
      function handleWindowResize() {
        setWindowSize(getWindowSize());
      }
      
      window.addEventListener('resize', handleWindowResize);
      
      return () => {
        window.removeEventListener('resize', handleWindowResize);
      };
    }, []);
  
    let LPWidth = windowSize.innerWidth * 0.84;
    console.log(LPWidth);




  return (
    <Container id='portfolio'>
        <MobileImg LPWidth={LPWidth} className='div1'  src="https://mir-s3-cdn-cf.behance.net/project_modules/fs/5c565b145216265.629df521a0d52.jpg"/>
        <MobileImg LPWidth={LPWidth} className='div2'  src="https://mir-s3-cdn-cf.behance.net/project_modules/2800_opt_1/d25cc7145216265.629a13e09a727.png"/>
        <MobileImg LPWidth={LPWidth} className='div3'  src="https://mir-s3-cdn-cf.behance.net/project_modules/1400/de22cd146265945.62acf8e42b1e9.gif"/>
        {/* <Row>
            <Column>
                <SmallerImg src="https://mir-s3-cdn-cf.behance.net/project_modules/2800_opt_1/d25cc7145216265.629a13e09a727.png"/>
                <SmallerImg data-aos="fade-up" src="https://mir-s3-cdn-cf.behance.net/project_modules/fs/5c565b145216265.629df521a0d52.jpg"/>
            </Column>
            <BiggerImg src='https://mir-s3-cdn-cf.behance.net/project_modules/1400/de22cd146265945.62acf8e42b1e9.gif'/>
        </Row> */}
    </Container>
  )
}


function getWindowSize() {
    const {innerWidth, innerHeight} = window;
    return {innerWidth, innerHeight};
  }
  


export default Portfolio