import React from 'react'
import styled from '@emotion/styled'
import {Link} from 'react-router-dom'


const Container = styled.div`
display: flex;
align-items: center;
flex-direction: column;
justify-content: space-between;
width: auto;
background-color:#202020;
height: auto;
// padding: 8rem 12.5rem;
padding: 2rem;
margin: 0;
scroll-snap-align: center;
@media(max-width: 700px){
    padding: 6rem 2rem; 
}
`
const SecLeft = styled.div`
display: flex;
align-items: center;
justify-content: space-around;
flex-direction: column;
gap:1.2rem;
margin-bottom: 2rem;

`
const SecCenter = styled.div`
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
color:#F2EEE3;
// font-size: 1rem;
// font-weight: 700;
`
const SecRight = styled.div`
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
`

const Links = styled.div`
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
`

const Img = styled.div`
display: flex;
align-items: center;
justify-content: center;
margin: 0 10px;
width:2.25rem;
height:2.25rem;
background-color: #F2EEE3;
border-radius: 50%;
margin-top: 20px;
`
// const Logo = styled.div`
// margin-bottom: 20px;
// :hover{
//     cursor: pointer;
// }
// `
const Text = styled.div`
color:#F2EEE3;
font-size: 1rem;
font-weight: 400;
font-family: roboto;

`
const Title = styled.div`
color:#F2EEE3;
font-size: 1.4rem;
font-family: roboto;
font-weight: 700;

:hover{
    cursor: pointer;
}
`
const Content = styled.div`
// margin: 10px 0;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
margin: 0.6rem 0;
`

