import React, { useState } from 'react'
import styled from '@emotion/styled'
import daniel from '../imgs/people/daniel.JPG'
import enzo from '../imgs/people/enzo.JPG'
import leticia from '../imgs/people/leticia.JPG'
import khellen from '../imgs/people/khellen.JPG'
import Cards from './Cards'
import './styles/QuemSomos.css'




const Row = styled.div`
  display: flex;
  flex-direction: row;
  margin: 40px 0;
  justify-content: space-between;
  // align-items: center;
  width: 100%;
  height: auto;
`
const Container = styled.div`
  display: flex;
  height: auto;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;

`
const Button = styled.button`
position: relative;
border: 2px solid #202020;
z-index: 1;
margin-top: 1rem;
height: 2.063rem;
&>span{
  padding: 0 1.125rem;
  font-size: 0.915rem;
  font-weight: regular;
  font-family: roboto;
  transition: all 0.25s ease;
  color: #202020;
}
:hover{
  cursor: pointer;
  color: white;
  &>span{
    color: white;
  }
}
&::after{
  display: flex;
  align-items: center;
  justify-content: center;
  content: '';
  position: absolute;
  top: 0;
  z-index: -1;
  left: 0;
  height: 100%;
  background-color: #202020;
  width: 0%;
  transition: all 0.25s ease;
}
&:hover::after{
  width: 100%;
}
`
const Title = styled.div`
  font-size: 1.829rem;
  font-weight: 700;
  font-family: roboto;
  color: #202020;
  margin-bottom: 1rem;

`
const Texto = styled.div`
  font-size: 0.915rem;
  font-weight: 300;
  color: #202020;
  font-family: roboto;
  //width: auto;
`

const Section = styled.div`
// margin: 40px 100px;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
width: 100%;
margin: 0;
padding: 0;
height:auto;
`
const QuemSomos = () => {

  const [visible, setVisible] = useState(false);
  const handleClick = () => setVisible(!visible);

  return (
    <Section id='quemSomos'>
        <Row>
          <Container>
            <Title>Quem Somos</Title>
            <div style={{display: 'flex',gap: "0.6rem", flexDirection: 'column'}}>
              <Texto>Muito mais que uma agência de marketing, nós somos um estúdio criativo, que te orienta e direciona a ter uma comunicação que crie conexões com você e o seu público.</Texto>
              <Texto>Somos criadores de ideias, processos e estratégias, que auxilia você, a comunicar sua mensagem, e deixar sua marca no mundo.</Texto>
            </div>
            <Button onClick={()=>(handleClick())} ><span>Saiba Mais</span></Button>
          </Container>
        </Row>
        <div className={visible ? "card-col visible":"card-col invisible"}>
            <Cards nome="Daniel M. Almeida" cargo="Diretoria" desc="Empreendedor desde 1998. Foi sócio na franquia de Desenvolvimento da Datasul Finanças, sócio-Fundador da empresa de consultoria Euax e do software Artia para Gestão de Projetos. " srcImg={daniel}/>
            <Cards nome="Khellen Kaztellarz" cargo="CEO" desc="Empreendedora há mais de dezesseis anos, atuou nos mercados do Brasil, Estados Unidos e Canadá.
            Meu propósito é ajudar você a encontrar o equilíbrio entre o êxito nos negócios e o tempo para si, a fim de desfrutar de forma harmoniosa da sua vida pessoal." srcImg={khellen}/>
            <Cards nome="Letícia Trovo" cargo="Designer Leader" desc="Letícia Trovo é Londrinense, Designer Gráfico, e Ilustradora responsável por toda a idealização e conceito das identidades visuais da Brief! 
            Apaixonada por criar coisas e construir soluções para os nossos projetos, é ela quem desenvolve e traz cor para os nossos trabalhos, sempre repleta de ideias inovadoras." srcImg={leticia}/>
            <Cards nome="Enzo Santiago" cargo="Designer Gráfico" desc="Natural do Rio de Janeiro, Designer Gráfico e Motion Designer. É o Responsável pela criação das identidades visuais, e por fazer as animações e edições de vídeo nos nossos projetos.
            É o criador de elementos visuais que trazem movimento e vida para o trabalho da Brief Studio." srcImg={enzo}/>
        </div>
    </Section>
  )
}

export default QuemSomos