import React from 'react'
import styled from '@emotion/styled'
import './styles/Cards.css'


const Title = styled.div`
    font-size: 1.5rem;
    font-weight: 700;
    font-family: 'roboto';
    text-align: right;

`

const Cargo = styled.div`
    font-size: 1.188rem;
    font-weight: 400;
    font-family: 'roboto';
    text-align: right;
`

const Texto = styled.div`
    font-size: 0.915rem;
    font-weight: 400;
    font-family: 'roboto';
    text-align: justify;
`

const Cards = ({nome,cargo,desc,srcImg}) => {

  return (
    <div className='Cards'>
        <div className='Cards-header'>
            <img src={srcImg} alt={nome}/>
            <div clasName="Cards-headerInfo">
                <Title>{nome}</Title>
                <Cargo>{cargo}</Cargo>
            </div>
        </div>
        <Texto>{desc}</Texto>
    </div>
  )
}

export default Cards