import {React} from 'react'
import styled from '@emotion/styled'
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { Link as LinkRoll } from 'react-scroll'
import './styles/Namebar.css'
import Footer from './Footer';
// import FooterMobile from './FooterMobile';




const Img = styled.img`
    object-fit: cover;
    object-position: center;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    transition: all 0.4s ease-in-out;
    :hover{
        transform: scale(0.95);
    }
`
const Name = styled.div`
    position: absolute;
    font-size: 2rem;
    font-weight: 700;
    transform: rotate(-90deg);
    bottom: 4.5rem;
    right: 9.375rem;
    z-index: 1000;
    color: #fff;

`
const Content = styled.div`
    position: relative;
    display: flex;
    width: 100%;
    height: 100vh;
    scroll-snap-align: center;
    .css-i4bv87-MuiSvgIcon-root{
        color: #F2EEE3;
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: rotate(-90deg);
        :hover{
            cursor: pointer;
        }
    }
`
const Button = styled.button`
position: relative;
border: 2px solid #202020;
z-index: 1;
height: 4.625rem;
&>span{
  padding: 0 2.063rem;
  font-size: 1.8rem;
  font-weight: regular;
  font-family: roboto;
  transition: all 0.25s ease;
  color: #202020;
}
:hover{
  cursor: pointer;
  color: white;
  &>span{
    color: white;
  }
}
&::after{
  display: flex;
  align-items: center;
  justify-content: center;
  content: '';
  position: absolute;
  top: 0;
  z-index: -1;
  left: 0;
  height: 100%;
  background-color: #202020;
  width: 0%;
  transition: all 0.25s ease;
}
&:hover::after{
  width: 100%;
}
`

const Namebar = () => {


  return (
    <div id='main_content'>
        <Content id='c1' >
            <Name>Brief</Name>
            <a href='https://www.behance.net/' style={{width: '100%', height: '100vh',margin: '0',padding: '0'}}><Img src='https://mir-s3-cdn-cf.behance.net/project_modules/1400/de22cd146265945.62acf8e42b1e9.gif'/></a>
            <LinkRoll to='c2' spy={true} smooth={true} offset={0} duration={1000}><ArrowBackIosNewIcon/></LinkRoll>
        </Content>
        <Content id='c2'>
            <Name>Hadar</Name>
            <a href='https://www.behance.net/' style={{width: '100%', height: '100vh',margin: '0',padding: '0'}}><Img src='https://mir-s3-cdn-cf.behance.net/project_modules/2800_opt_1/d25cc7145216265.629a13e09a727.png'/></a>
            <LinkRoll to='c3' spy={true} smooth={true} offset={0} duration={1000}><ArrowBackIosNewIcon/></LinkRoll>
        </Content>
        <Content id='c3'>
            <Name>Hadar</Name>
            <a href='https://www.behance.net/' style={{width: '100%', height: '100vh',margin: '0',padding: '0'}}><Img src='https://mir-s3-cdn-cf.behance.net/project_modules/fs/5c565b145216265.629df521a0d52.jpg'/></a>
            <LinkRoll to='c1' spy={true} smooth={true} offset={0} duration={1000}><ArrowBackIosNewIcon/></LinkRoll>
        </Content>
        <div id='c4' style={{scrollSnapAlign: 'center',display: 'flex', justifyContent: 'center',alignItems: 'center',width: '100%',height: '42vh'}}>
            <Button onClick={()=>{window.location.replace("https://www.behance.net/")}}><span>Saiba Mais</span></Button>
        </div>
        <Footer id='c4' style={{scrollSnapAlign: 'center'}}/>
    </div>
  )
}

export default Namebar