import React from 'react'
import styled from '@emotion/styled'
import './styles/Ferramentas.css'

const Container = styled.div`
    display: flex;
    width: auto;
    flex-direction: column;
    width:100%;
    align-items: flex-start;
    h3{
        font-size: 1.829rem;
        font-weight: 500;
        margin: 1rem 0;
        padding: 0;
        font-family: roboto;
    }
    `
    const Section = styled.div`
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    `
const Card = styled.div`
display: flex;
width: 40%;
margin:20px 0;
align-items: center;
@media(max-width: 700px){
    width: 80%;
}
`
const Img = styled.div`
&> *{
    width:100%;
}
`
const Title = styled.div`
font-size: 1.105rem;
font-weight: 700;
font-family: roboto;

`
const Desc = styled.div`
font-size: 0.8rem;
font-weight: 300;
font-family: roboto;

`
const Info = styled.div`
display: flex;
flex-direction: column;
margin-left: 20px;
`

