import styled from '@emotion/styled'
import React, { useState, useEffect } from 'react'
import Typewriter from 'typewriter-effect';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { Link } from 'react-scroll'


const Section = styled.div`
    width: 54%;
    margin: 0 12.5rem;
    padding: 0;
    .Typewriter{
        text-align: center;
    }
    .Typewriter__wrapper,.Typewriter__cursor{
        font-family: 'Roboto', sans-serif;
        font-height: 400px;
        color: #F2EEE3;
        font-size: 3rem;
        margin: 0;
        padding: 0;
        // display: flex;
        // align-items: center;
        // justify-content: center;
        text-align: center;
        @media (max-width: 770px) {
            font-size: 28px;
            
        }
    }
    @media (max-width: 770px) {
        margin: 0 0rem;
        width: 76%;
    }
    
`

const Container = styled.div`
    position: relative;
    margin: 0;
    padding: 0;
    height: 84vh;
    // min-height:400px;
    width: 100%;
    background-color: #F9432B;
    display: flex;
    align-items: center;
    justify-content: center;
    .css-i4bv87-MuiSvgIcon-root{
        color: #F2EEE3;
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: rotate(-90deg);
        :hover{
            cursor: pointer;
        }
    }
    @media (max-width: 770px) {
        height: 84vh;
    }
`


const LandingPage = () => {


  
    const [windowSize, setWindowSize] = useState(getWindowSize());
  
    useEffect(() => {
      function handleWindowResize() {
        setWindowSize(getWindowSize());
      }
      
      window.addEventListener('resize', handleWindowResize);
      
      return () => {
        window.removeEventListener('resize', handleWindowResize);
      };
    }, []);
  
    let LPHeight = windowSize.innerHeight * 0.84;
    console.log(LPHeight);



  return (
    <Container>
        <Section>
            <Typewriter className = "typewriter"
            onInit={(typewriter) => typewriter.changeDelay(50).typeString("Prazer, somos os melhores em transmitir o valor da sua marca para o mundo, somos Brief.").start()}
            />
            <Link to='portfolio' spy={true} smooth={true} offset={0} duration={1000}><ArrowBackIosNewIcon/></Link>
        </Section>
    </Container>
  )
}


function getWindowSize() {
    const {innerWidth, innerHeight} = window;
    return {innerWidth, innerHeight};
  }
  


export default LandingPage